/* dm-sans-regular - latin */
@font-face {
    font-display: swap;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    src: url(../../fonts/dm-sans-v14-latin-regular.woff2) format('woff2');
}

